<template>
	<view class="page-container">
		<!-- 购物车标题栏 -->
		<view class="cart-header">
			<navigator open-type="navigateBack" class="back-icon">
				<view class="back-icon">
					<image src="/static/icons/shopping icon/icon-arrow-left备份.svg" class="icon-svg"></image>
				</view>
			</navigator>
			<text class="header-title">购物车</text>
			<view class="header-right" @click="goToLogin" v-if="!isLoggedIn">
				<text class="login-text">登录</text>
			</view>
		</view>
		
		<view class="cart-container">
			<!-- 未登录提示 -->
			<view class="login-prompt" v-if="!isLoggedIn">
				<view class="prompt-icon">🛒</view>
				<view class="prompt-title">登录后查看购物车</view>
				<view class="prompt-desc">登录后可同步您的购物车商品</view>
				<button class="login-btn" @click="goToLogin">立即登录</button>
			</view>
			
			<!-- 已登录状态 -->
			<template v-else>
			<!-- 满减促销信息 -->
			<view class="promotion-bar">
				<view class="back-icon">
					<image src="/static/tabbar/shopping-bag (2).svg" class="icon-svg"></image>
				</view>
				<text>全场满199元立减30元，满299元立减50元</text>
			</view>
			
			<!-- 购物车有商品时显示 -->
			<block v-if="cartItems.length > 0">
				<!-- 购物车项目标题 -->
				<view class="cart-section-title">
					<view class="back-icon">
						<image src="/static/tabbar/shopping-bag (2).svg" class="icon-svg"></image>
					</view>
					<text>瑶山好物 ({{cartItems.length}}件商品)</text>
				</view>
				
				<!-- 购物车商品列表 -->
					<view class="cart-item" v-for="(item, index) in cartItems" :key="item._id">
						<view class="item-checkbox" @click="toggleItemSelect(item._id, !item.selected)">
						<view class="custom-checkbox" :class="{ checked: item.selected }">
							  <image src="/static/icons/shopping icon/check.svg" v-if="item.selected"></image>
							</view>
						</view>
						
						<image :src="item.productInfo.image || '/static/default-product.jpg'" class="item-image"></image>
						
					<view class="item-info">
							<text class="item-name">{{item.productInfo.name || '商品名称'}}</text>
							<text class="item-specs">{{item.productInfo.specs || '默认规格'}}</text>
							
						<view class="item-price-section">
								<text class="item-price">¥{{item.productInfo.price || 0}}</text>
								
							<view class="quantity-control">
									<view class="quantity-btn" @click="decreaseQuantity(item._id, item.quantity)">-</view>
									<input 
										class="quantity-input" 
										type="number" 
										:value="item.quantity"
										@blur="onQuantityChange(item._id, $event.detail.value)"
									/>
									<view class="quantity-btn" @click="increaseQuantity(item._id, item.quantity)">+</view>
								</view>
							</view>
						</view>
						
						<view class="item-actions" @click="removeItem(item._id)">
							<text class="iconfont icon-trash-alt"></text>
					</view>
				</view>
				
				<!-- 猜你喜欢 -->
				<view class="cart-recommendations">
					<view class="recommendations-title">猜你喜欢</view>
					<view class="recommendations-grid">
						<view class="recommendation-item" v-for="(item, index) in recommendItems" :key="index" @click="addToCartFromRecommend(item)">
							<image :src="item.image" :alt="item.name" class="recommendation-image"></image>
							<text class="recommendation-name">{{item.name}}</text>
							<text class="recommendation-price">¥{{item.price}}</text>
						</view>
					</view>
				</view>
			</block>
			
			<!-- 空购物车状态 -->
				<view class="empty-cart" v-if="cartItems.length === 0 && !loading">
				<text class="iconfont icon-shopping-cart empty-cart-icon"></text>
				<text class="empty-cart-text">购物车还是空的，去挑选心仪的商品吧~</text>
				<view class="go-shopping-btn" @click="goShopping">去购物</view>
			</view>
				
				<!-- 加载状态 -->
				<view class="loading-state" v-if="loading">
					<text>加载中...</text>
				</view>
			</template>
		</view>
		
		<!-- 底部结算栏 -->
		<view class="cart-footer" v-if="isLoggedIn && cartItems.length > 0">
			<view class="select-all" @click="toggleSelectAll">
				<view class="custom-checkbox" :class="{ checked: isAllSelected }">
					<image src="/static/icons/shopping icon/check.svg" v-if="isAllSelected"></image>
				</view>
				<text>全选</text>
			</view>
			<view class="cart-total">
				<text class="total-price">¥{{totalPrice}}</text>
				<text class="price-details">总计: ¥{{totalPrice}} · 已优惠: ¥{{discount}}</text>
			</view>
			<view class="checkout-btn" @click="checkout">结算({{selectedCount}})</view>
		</view>
	</view>
</template>

<script setup>
	import { ref, computed, onMounted, onUnmounted, getCurrentInstance, nextTick } from 'vue'
	import { onShow } from '@dcloudio/uni-app'
	import { getAllProducts } from '@/data/products.js'
	
	const { proxy } = getCurrentInstance()
	
	// 响应式数据
	const cartItems = ref([])
	const loading = ref(false)
	const discount = ref(3)
	const userToken = ref('')
	const userInfo = ref(null)
	
	const recommendItems = ref([
					{
						id: 4,
						name: '瑶山野生香菇',
						price: 78,
						image: '/static/static2/3944.jpg_wh860.jpg'
					},
					{
						id: 5,
						name: '瑶山古法山茶油',
						price: 188,
						image: '/static/static2/v2-a582c6ef03ee0473ff546458328c18b6_720w.png'
					},
					{
						id: 6,
						name: '瑶山有机稻米',
						price: 128,
						image: '/static/static2/photo-1515339760107-1952b7a08454.avif'
					}
	])
	
	// 计算属性 - 基于本地响应式数据计算
	const isLoggedIn = computed(() => {
		return !!userToken.value
	})
	
	const selectedCount = computed(() => {
		return cartItems.value
			.filter(item => item.selected)
			.reduce((total, item) => total + item.quantity, 0)
	})
	
	const totalPrice = computed(() => {
		return cartItems.value
					.filter(item => item.selected)
			.reduce((total, item) => total + (item.productInfo.price || 0) * item.quantity, 0)
	})
	
	const isAllSelected = computed(() => {
		return cartItems.value.length > 0 && cartItems.value.every(item => item.selected)
	})
	
	// 检查登录状态
	const checkLoginStatus = () => {
		userToken.value = uni.getStorageSync('userToken') || ''
		userInfo.value = uni.getStorageSync('userInfo') || null
	}
	
	// 加载购物车数据
	const loadCartData = async () => {
		if (!userToken.value) {
			cartItems.value = []
			return
		}
		
		loading.value = true
		
		try {
			const result = await uniCloud.callFunction({
				name: 'user-cart',
				data: {
					action: 'getCart',
					token: userToken.value
				}
			})
			
			if (result.result.errCode === 0) {
				cartItems.value = result.result.data || []
			} else {
				console.error('获取购物车失败:', result.result.errMsg)
				if (result.result.errCode === 10001) {
					// token无效，清除登录状态
					uni.removeStorageSync('userToken')
					uni.removeStorageSync('userInfo')
					checkLoginStatus()
				}
			}
		} catch (error) {
			console.error('加载购物车数据失败:', error)
			uni.showToast({
				title: '加载失败，请重试',
				icon: 'none'
			})
		} finally {
			loading.value = false
		}
	}
	
			// 切换商品选中状态
	const toggleItemSelect = async (cartItemId, selected) => {
		try {
			const result = await uniCloud.callFunction({
				name: 'user-cart',
				data: {
					action: 'toggleSelect',
					token: userToken.value,
					cartItemId,
					selected
				}
			})
			
			if (result.result.errCode === 0) {
				await loadCartData()
			} else {
				uni.showToast({
					title: result.result.errMsg,
					icon: 'none'
				})
			}
		} catch (error) {
			console.error('切换选中状态失败:', error)
		}
	}
	
			// 切换全选
	const toggleSelectAll = async () => {
		const newSelectState = !isAllSelected.value
		
		// 批量更新所有商品的选中状态
		const promises = cartItems.value.map(item => 
			toggleItemSelect(item._id, newSelectState)
		)
		
		await Promise.all(promises)
	}
	
	// 更新商品数量
	const updateQuantity = async (cartItemId, newQuantity) => {
		try {
			const result = await uniCloud.callFunction({
				name: 'user-cart',
				data: {
					action: 'updateQuantity',
					token: userToken.value,
					cartItemId,
					newQuantity
				}
			})
			
			if (result.result.errCode === 0) {
				await loadCartData()
			} else {
				uni.showToast({
					title: result.result.errMsg,
					icon: 'none'
				})
			}
		} catch (error) {
			console.error('更新数量失败:', error)
		}
	}
	
			// 减少商品数量
	const decreaseQuantity = async (cartItemId, currentQuantity) => {
		if (currentQuantity > 1) {
			await updateQuantity(cartItemId, currentQuantity - 1)
				} else {
					uni.showModal({
						title: '提示',
						content: '确定要移除该商品吗？',
				success: async (res) => {
							if (res.confirm) {
						await removeItem(cartItemId)
							}
						}
			})
		}
				}
	
			// 增加商品数量
	const increaseQuantity = async (cartItemId, currentQuantity) => {
		await updateQuantity(cartItemId, currentQuantity + 1)
	}
	
	// 处理数量输入框变化
	const onQuantityChange = async (cartItemId, newQuantity) => {
		const quantity = parseInt(newQuantity)
		if (quantity > 0) {
			await updateQuantity(cartItemId, quantity)
		} else {
			await removeItem(cartItemId)
		}
	}
	
			// 移除商品
	const removeItem = async (cartItemId) => {
				uni.showModal({
					title: '提示',
					content: '确定要移除该商品吗？',
			success: async (res) => {
						if (res.confirm) {
					try {
						const result = await uniCloud.callFunction({
							name: 'user-cart',
							data: {
								action: 'removeItem',
								token: userToken.value,
								cartItemId
							}
						})
						
						if (result.result.errCode === 0) {
							await loadCartData()
							uni.showToast({
								title: '已移除',
								icon: 'success'
							})
						} else {
							uni.showToast({
								title: result.result.errMsg,
								icon: 'none'
							})
						}
					} catch (error) {
						console.error('移除商品失败:', error)
					}
						}
					}
		})
	}
	
			// 从推荐添加商品
	const addToCartFromRecommend = async (item) => {
		if (!userToken.value) {
			uni.showToast({
				title: '请先登录',
				icon: 'none'
			})
			return
		}
		
		try {
			const result = await uniCloud.callFunction({
				name: 'user-cart',
				data: {
					action: 'addToCart',
					token: userToken.value,
					productId: item.id,
						quantity: 1,
					productInfo: item
				}
			})
				
			if (result.result.errCode === 0) {
				await loadCartData()
				uni.showToast({
					title: '已加入购物车',
					icon: 'success'
				})
			} else {
				uni.showToast({
					title: result.result.errMsg,
					icon: 'none'
				})
			}
		} catch (error) {
			console.error('添加到购物车失败:', error)
		}
	}
	
	// 跳转到登录页面
	const goToLogin = () => {
		uni.navigateTo({
			url: '/pages/login/login'
		})
	}
	
			// 跳转到商城页面
	const goShopping = () => {
				uni.switchTab({
					url: '/pages/shopping/shopping'
		})
	}
	
			// 结算
	const checkout = () => {
		if (selectedCount.value === 0) {
					uni.showToast({
						title: '请选择商品',
						icon: 'none'
			})
			return
				}
				
				// 获取选中的商品
		const selectedItems = cartItems.value.filter(item => item.selected)
		
		// 将选中的商品数据传递到结算页面
		uni.setStorageSync('checkout_items', selectedItems)
				
				// 跳转到确认订单页面
				uni.navigateTo({
					url: '/pages/shopping/checkout/checkout'
		})
	}
	
	// 生命周期
	onMounted(async () => {
		checkLoginStatus()
		if (userToken.value) {
			await loadCartData()
			}
	})
	
	onUnmounted(() => {
		// 移除事件监听
		uni.$off('cartUpdated', handleCartUpdate)
	})
	
	// 页面显示时刷新购物车数据
	onShow(async () => {
		checkLoginStatus()
		if (userToken.value) {
			await loadCartData()
	}
	})
</script>

